*,
*:before,
*:after {
  box-sizing: border-box;
}

:root {
  --image-height: 350px;
  --clr-white: #fff;
  --clr-light-grey: #e2e2e2;
  --clr-purple: #6c567b;
}

html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
  font-family: "Lato", sans-serif;
  background-color: #f7fbfb;
  line-height: 1.65;
  color: #010a43;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.experiments {
  display: block;
  width: 95%;
  max-width: 1400px;
  margin: 0 auto 1.4rem;
}

.experiments__header {
  margin: 2.75rem 0 0;
  line-height: 1.15;
  font-size: 2.488em;
}

.experiments__section {
  margin: 2rem 0;
}

.experiments__section__title {
  position: relative;
  width: 95%;
  width: fit-content;
  margin: 0;
  margin-bottom: 2rem;
  font-size: 1.7em;
  line-height: 1.15;
  font-weight: 400;

  &:after {
    content: "";
    position: absolute;
    display: block;
    height: 5px;
    width: 100%;
    bottom: -1px;
    background-color: #ffcc00;
    background-image: linear-gradient(90deg, #ffcc00 0%, #cc0066 100%);
  }
}

.experiments__list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  grid-gap: 1rem;
}

.experiments__list__item {
  overflow: hidden;
  background-color: var(--clr-white);
  border: 1px solid var(--clr-light-grey);
  border-radius: 4px;
  box-shadow: 0 0 0 1px rgba(255, 255, 255, 0.1), 0 2px 4px 0 rgba(14, 30, 37, 0.12);

  &--single {
    grid-column: 1 / 2;
  }

  img {
    object-fit: cover;
    width: 100%;
    display: block;
    border-style: none;
    height: auto;

    @media (min-width: 700px) {
      height: var(--image-height);
    }
  }
}

.experiment__name {
  line-height: 1.15;
  font-size: 1.3em;
  margin: 1.5rem 0;
}

.item-info {
  border-top: 1px solid var(--clr-light-grey);
  padding: 1rem;
  display: flex;
  flex-direction: column;

  .html,
  .css,
  .js,
  .react {
    display: inline-block;
    font-size: 1rem;
    text-transform: uppercase;
    margin-right: 1rem;
    font-weight: bold;
  }

  .html {
    color: #3c6f9c;
  }

  .css {
    color: #b030b0;
  }

  .js {
    color: #6e5773;
  }

  .react {
    color: #bd245f;
  }
}

.experiment__links {
  margin-top: auto;
}

.link {
  display: inline-block;
  padding: 0.7em 0.8em;
  opacity: 0.8;
  text-decoration: none;
  cursor: pointer;
  transition: all 0.3s ease-out;
  text-transform: uppercase;
  font-weight: bold;
  letter-spacing: 1px;
  font-size: 0.875rem;
  border: 2px solid var(--clr-purple);

  &:hover {
    opacity: 1;
  }
  &:active {
    background-color: var(--clr-purple);
    color: var(--clr-white);
  }
}

.link-primary {
  background-color: var(--clr-purple);
  color: var(--clr-white);
}

.link-secondary {
  margin-left: 10px;
  color: var(--clr-purple);
  background-color: var(--clr-white);
  &:hover {
    box-shadow: inset 0 0 2px var(--clr-purple);
  }
}
